body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.result .page-header {
  color: #fff;
  background: #191818;
  margin: 0;
  padding: 10px 20px; }

.result .editor-pane {
  position: fixed;
  top: 57px;
  left: 0;
  bottom: 0;
  width: 50%;
  height: auto;
  overflow: auto;
  box-shadow: -10px 2px 6px 10px rgba(0, 0, 0, 0.4); }

.result .result-pane > div {
  position: fixed;
  top: 57px;
  right: 0;
  left: 50%;
  bottom: 0;
  overflow: auto;
  padding: 10px;
  padding-left: 20px;
  color: #444;
  font-family: Georgia, Palatino, 'Palatino Linotype', Times, 'Times New Roman', serif;
  font-size: 16px;
  line-height: 1.5em; }

.result .editor textarea {
  padding: 20px; }

.result .editor-pane textarea {
  min-height: 500px; }

.result .markdown-controls {
  position: relative;
  z-index: 5;
  text-align: right;
  color: #fff;
  float: right; }

.result .markdown-controls form {
  background-color: rgba(39, 40, 34, 0.5);
  margin-right: 20px; }

.result .markdown-controls legend {
  border-bottom: 0;
  color: #fff;
  font-size: 1.25em;
  margin: 0;
  padding: 10px 0 0 0; }

.result .CodeMirror {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  font-size: 16px; }

.result pre {
  border: 1px solid #ccc; }

.result blockquote {
  color: #666;
  margin: 0;
  padding-left: 3em;
  border-left: 0.5em #eee solid; }

.result tr {
  border-top: 1px solid #c6cbd1;
  background: #fff; }

.result th {
  background: #eee; }

.result th,
.result td {
  padding: 6px 13px;
  border: 1px solid #dfe2e5; }

.result table tr:nth-child(2n) {
  background: #f6f8fa; }
